___CSS_LOADER_EXPORT___.push([module.id, `:root {    
    font-size: 16px;
    font-family: 'Courier New', Courier, monospace;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    scrollbar-color: #2F4858 azure;
    scrollbar-width: thin;
}

h1 {
    line-height: 1.9rem;
}

body {
    background-color: #cadbe4;    
    height: 100vh;
    width: 100vw;    
    display: flex;
    flex-direction: column;    
}

#app-grid {
    flex-grow: 1;
    display: grid;
    grid-template-columns: 58px minmax(275px, 20%) minmax(400px, 1fr) 58px;
    grid-template-rows: 25px minmax(210px, 1fr) 20px 3fr 25px;
    overflow: auto;
}

#app-grid > div {
    display: flex;      
}

#header {
    background-color: #80b3d1;    
    grid-column: 1 / 5;
    grid-row: 1 / 2;    
}

#upper-left-side {
    background-color: #cadbe4;
    flex-direction: column;   
    grid-column: 1 / 2;
    grid-row: 2 / 4;      
    padding: 0 4px 0 4px; 
}

#lower-left-side {
    background-color: #cadbe4;
    flex-direction: column;   
    grid-column: 1 / 2;
    grid-row: 4 / 5;      
    padding: 0 4px 0 4px;   
}

#right-side {
    background-color: #cadbe4;
    flex-direction: column;    
    grid-column: 4 / 5;
    grid-row: 2 / 5;
    padding: 0.8em 4px 0 0px;
    gap: 5px;  
}

#period {
    background-color: #cadbe4;    
    grid-column: 2 / 3;
    grid-row: 2 / 3;    
    flex-direction: column;
    justify-content: space-between;    
    padding: 0.8em 0 0.8em 0;             
}

#period div {
    display: flex;
    justify-content: space-between;
    gap: 15px;    
}

#period div .period-btn {
    flex-grow: 1;    
}

#period div .prio-btn {
    width: 50px;
    background-repeat: no-repeat;
    background-position: center;
    background-size: 75%;      
}

#period button {    
    font-family: 'Courier New', Courier, monospace;    
    background-color: azure;
    border: 2px outset #80b3d1;    
    font-weight: 600;
    padding-top: 5px;
    padding-bottom: 5px;
    border-radius: 5px;
    box-shadow: -1px 2px 3px 1px #9aafb9;
    font-size: 1.2em;        
}

#period div:nth-child(2) button:first-child, 
#period div:nth-child(4) button:first-child {
    background-size: 50%;
}

#collection-controls {    
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    gap: 1em;    
}

.collection-control-btn,
#to-do-controls button {
    background-color: azure;
    border-color: #80b3d1;
    background-repeat: no-repeat;
    background-position: center;
    background-size: 30% 38%;    
    border-radius: 5px;
    display: flex;
    flex-direction: column;
    padding: 18px;       
    box-shadow: -1px 2px 3px 1px #9aafb9;  
}

#to-do-controls button:nth-child(3) {
    background-size: 45% 55%; 
}

#collections-header {    
    background-color: #cadbe4;
    color: #2F4858;
    grid-column: 2 / 3;
    grid-row: 3 / 4;
    display: flex;             
}

#collections-header div {
    width: 95%;
    display: flex;
    justify-content: center;       
}

#collections {
    background-color: #cadbe4;
    grid-column: 2 / 3;
    grid-row: 4 / 5; 
    flex-direction: column;    
    align-items: flex-start;
    gap: 0.6em;        
    margin-bottom: 0.8em;
    overflow: auto;  
}

.collection {
    background-color: azure;
    border: 2px outset #80b3d1;    
    display: flex;
    flex-direction: column;
    width: 100%;    
    border-radius: 5px; 
    padding: 0.3em;
    gap: 2px;    
    box-shadow: -1px 2px 3px 1px #9aafb9;       
}

.collection-header {       
    display: flex;
    align-items: center;         
}

.collection-header-txt {    
    background-color: azure;    
    font-size: 1.3rem;    
    text-align: left;
    padding: 0 3px 0 3px;
    overflow: hidden;         
}

.collection-header button {
    background-color: azure;
    border: 2px solid #80b3d1;
    background-repeat: no-repeat;
    background-position: center;
    background-size: 30% 38%;    
    border-radius: 5px;
    display: flex;
    flex-direction: column;
    padding: 0px 6px 0px 6px;
    margin-bottom: 2px;
    font-size: 1.1rem;
    font-weight: 800;    
}

[data="last-clicked-collection"] {
    border: 2px dashed #80b3d1;
    color: #80b3d1; 
    font-weight: 600;
}

.progress-div {
    flex-grow: 1;
    display: flex;
    justify-content: flex-end;
    padding-right: 6px;
}

.todo-div {
    display: flex;
    flex-direction: column;    
}

[minimize="yes"] .todo-div {
    display: none;
}

.to-do {   
    display: flex;
    align-items: center;
    justify-content: flex-start;
    border-bottom: 1px dashed #80b3d1;
    padding: 4px 0 4px 0;       
}

.to-do p {     
    display: flex;
    align-items: center;    
    max-height: 75px;
    padding-left: 4px;
    padding-right: 4px;
    overflow-x: hidden;      
}


[data="last-clicked-todo"] p {
    color: #2F4858;    
    border-left: 2px solid #2F4858;        
    border-right: 2px solid #2F4858;     
}

.todo-header-subject {
    white-space: pre;
}

.to-do div {
    min-width: 30px;
    
}

.clock {
    display: flex;
    justify-content: center;
    height: 25px;      
}

.circle {
    display: flex;
    justify-content: center;
    height: 12px;    
}

.snail {
    display: flex;
    justify-content: center;    
    height: 19px;   
}

.check {
    display: flex;
    justify-content: center;    
    height: 13px;
    margin-bottom: 3px;   
}

.priority-icon-container img {
    height: 100%;
    width: 100%;    
}

.empty {
    color: lightslategray;
    font-style: italic;
    padding: 3px 0 0 3px;
}

[minimize="yes"] .empty {
    display: none;
}

#content {
    background-color: #cadbe4;
    flex-direction: column;    
    grid-column: 3 / 4;
    grid-row: 2 / 5;
    padding: 0.8em 4px 0.8em 0.8em;
}

#content-area-header {
    background-color: #80b3d1;
    border: 2px solid #80b3d1;    
    box-shadow: -1px 0px 3px 1px #9aafb9;
    border-radius: 5px;
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;     
    display: flex;
    justify-content: center;
    align-items: center;   
    height: fit-content;     
    overflow-wrap: break-word; 
    position: relative;         
}

#content-area-header div {
    height: fit-content;
}

#content-area-header div p { 
    font-size: 1.8em;
    padding: 3px 0 3px 0;
    overflow: hidden;
    text-align: center;
}

#content-area-header button {
    position: absolute;
    right: 5px;
    display: none;
    width: fit-content;        
    background-color: #cadbe4;
    border: 2px solid #80b3d1;  
    border-radius: 5px;
    padding: 4px;
    margin-right: 8px;    
    font-size: 1.05rem;    
    font-weight: 800;    
}

.content-container {    
    background-color: azure;
    min-height: fit-content;
    max-height: 100%;    
    width: 100%;
    border: 2px outset #80b3d1;
    border-top: none;
    border-radius: 5px;
    border-top-left-radius: 0px;
    border-top-right-radius: 0px;
    box-shadow: -1px 2px 3px 1px #9aafb9;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
    padding: 2px;
    gap: 2px;
    overflow: auto;
    scrollbar-color: #2F4858 #80b3d1;
    scrollbar-width: thin;    
}

.to-do-details {
    border: 1px solid #80b3d1;
    min-height: 300px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 15px;
    gap: 0.3em;
    overflow: auto;    
}

#right-side p {
    text-align: center;
    color: #2F4858;
}

#to-do-controls {
    display: flex;
    flex-direction: column;
    gap: 1em; 
}

#footer {
    background-color: #2F4858;     
    grid-column: 1 / 5;
    grid-row: 5 / 6;    
}

#dialog-todo {
    border: 2px outset #80b3d1;
    border-radius: 5px;
    box-shadow: -1px 2px 3px 1px #9aafb9;   
    width: 200px;
    padding: 10px;
    position: absolute; 
    top: 60px;
    left: calc(100% - 200px - 60px);    
}

.dialog-header {
    display: flex;
    justify-content: space-between;
}

dialog h2 {
    border-bottom: 1px solid black;
}

dialog form {
    padding-top: 10px;    
    display: flex;
    flex-direction: column;
    gap: 10px;    
}

dialog p {
    display: flex;
    flex-direction: column;
    gap: 2px;
}

dialog form > div {
    display: flex;
    flex-grow: 1;
    justify-content: space-between;        
}

dialog button {
    background-color: azure;
    border-color: #80b3d1;   
    border-radius: 5px;
    padding: 2px 6px 2px 6px;    
}

dialog p button {
    background-color: azure;
    border-color: #80b3d1;   
    border-radius: 5px;
    width: 40%;    
}

dialog input {    
    display: flex;
    flex-direction: column;
    width: 95%;
    height: 1.4rem;
}

dialog input[type="date"] {    
    display: flex;
    flex-direction: row;
    width: 65%;
    height: 1.4rem;
}


dialog textarea {
    max-width: 95%;
    min-width: 95%;
    min-height: 1.4rem;
    max-height: 200px;
    padding: 5px;
}
