kground-color: azure;\n    border: 2px outset #80b3d1;    \n    font-weight: 600;\n    padding-top: 5px;\n    padding-bottom: 5px;\n    border-radius: 5px;\n    box-shadow: -1px 2px 3px 1px #9aafb9;\n    font-size: 1.2em;        \n}\n\n#period div:nth-child(2) button:first-child, \n#period div:nth-child(4) button:first-child {\n    background-size: 50%;\n}\n\n#collection-controls {    \n    flex-grow: 1;\n    display: flex;\n    flex-direction: column;\n    gap: 1em;    \n}\n\n.collection-control-btn,\n#to-do-controls button {\n    background-color: azure;\n    border-color: #80b3d1;\n    background-repeat: no-repeat;\n    background-position: center;\n    background-size: 30% 38%;    \n    border-radius: 5px;\n    display: flex;\n    flex-direction: column;\n    padding: 18px;       \n    box-shadow: -1px 2px 3px 1px #9aafb9;  \n}\n\n#to-do-controls button:nth-child(3) {\n    background-size: 45% 55%; \n}\n\n#collections-header {    \n    background-color: #cadbe4;\n    color: #2F4858;\n    grid-column: 2 / 3;\n    grid-row: 3 / 4;\n    display: flex;             \n}\n\n#collections-header div {\n    width: 95%;\n    display: flex;\n    justify-content: center;       \n}\n\n#collections {\n    background-color: #cadbe4;\n    grid-column: 2 / 3;\n    grid-row: 4 / 5; \n    flex-direction: column;    \n    align-items: flex-start;\n    gap: 0.6em;        \n    margin-bottom: 0.8em;\n    overflow: auto;  \n}\n\n.collection {\n    background-color: azure;\n    border: 2px outset #80b3d1;    \n    display: flex;\n    flex-direction: column;\n    width: 100%;    \n    border-radius: 5px; \n    padding: 0.3em;\n    gap: 2px;    \n    box-shadow: -1px 2px 3px 1px #9aafb9;       \n}\n\n.collection-header {       \n    display: flex;\n    align-items: center;         \n}\n\n.collection-header-txt {    \n    background-color: azure;    \n    font-size: 1.3rem;    \n    text-align: left;\n    padding: 0 3px 0 3px;\n    overflow: hidden;         \n}\n\n.collection-header button {\n    background-color: azure;\n    border: 2px solid #80b3d1;\n    background-repeat: no-repeat;\n    background-position: center;\n    background-size: 30% 38%;    \n    border-radius: 5px;\n    display: flex;\n    flex-direction: column;\n    padding: 0px 6px 0px 6px;\n    margin-bottom: 2px;\n    font-size: 1.1rem;\n    font-weight: 800;    \n}\n\n[data=\"last-clicked-collection\"] {\n    border: 2px dashed #80b3d1;\n    color: #80b3d1; \n    font-weight: 600;\n}\n\n.progress-div {\n    flex-grow: 1;\n    display: flex;\n    justify-content: flex-end;\n    padding-right: 6px;\n}\n\n.todo-div {\n    display: flex;\n    flex-direction: column;    \n}\n\n[minimize=\"yes\"] .todo-div {\n    display: none;\n}\n\n.to-do {   \n    display: flex;\n    align-items: center;\n    justify-content: flex-start;\n    border-bottom: 1px dashed #80b3d1;\n    padding: 4px 0 4px 0;       \n}\n\n.to-do p {     \n    display: flex;\n    align-items: center;    \n    max-height: 75px;\n    padding-left: 4px;\n    padding-right: 4px;\n    overflow-x: hidden;      \n}\n\n\n[data=\"last-clicked-todo\"] p {\n    color: #2F4858;    \n    border-left: 2px solid #2F4858;        \n    border-right: 2px solid #2F4858;     \n}\n\n.todo-header-subject {\n    white-space: pre;\n}\n\n.to-do div {\n    min-width: 30px;\n    \n}\n\n.clock {\n    display: flex;\n    justify-content: center;\n    height: 25px;      \n}\n\n.circle {\n    display: flex;\n    justify-content: center;\n    height: 12px;    \n}\n\n.snail {\n    display: flex;\n    justify-content: center;    \n    height: 19px;   \n}\n\n.check {\n    display: flex;\n    justify-content: center;    \n    height: 13px;\n    margin-bottom: 3px;   \n}\n\n.priority-icon-container img {\n    height: 100%;\n    width: 100%;    \n}\n\n.empty {\n    color: lightslategray;\n    font-style: italic;\n    padding: 3px 0 0 3px;\n}\n\n[minimize=\"yes\"] .empty {\n    display: none;\n}\n\n#content {\n    background-color: #cadbe4;\n    flex-direction: column;    \n    grid-column: 3 / 4;\n    grid-row: 2 / 5;\n    padding: 0.8em 4px 0.8em 0.8em;\n}\n\n#content-area-header {\n    background-color: #80b3d1;\n    border: 2px solid #80b3d1;    \n    box-shadow: -1px 0px 3px 1px #9aafb9;\n    border-radius: 5px;\n    border-bottom-left-radius: 0;\n    border-bottom-right-radius: 0;     \n    display: flex;\n    justify-content: center;\n    align-items: center;   \n    height: fit-content;     \n    overflow-wrap: break-word; \n    position: relative;         \n}\n\n#content-area-header div {\n    height: fit-content;\n}\n\n#content-area-header div p { \n    font-size: 1.8em;\n    padding: 3px 0 3px 0;\n    overflow: hidden;\n    text-align: center;\n}\n\n#content-area-header button {\n    position: absolute;\n    right: 5px;\n    display: none;\n    width: fit-content;        \n    background-color: #cadbe4;\n    border: 2px solid #80b3d1;  \n    border-radius: 5px;\n    padding: 4px;\n    margin-right: 8px;    \n    font-size: 1.05rem;    \n    font-weight: 800;    \n}\n\n.content-container {    \n    background-color: azure;\n    min-height: fit-content;\n    max-height: 100%;    \n    width: 100%;\n    border: 2px outset #80b3d1;\n    border-top: none;\n    border-radius: 5px;\n    border-top-left-radius: 0px;\n    border-top-right-radius: 0px;\n    box-shadow: -1px 2px 3px 1px #9aafb9;\n    display: grid;\n    grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));\n    padding: 2px;\n    gap: 2px;\n    overflow: auto;\n    scrollbar-color: #2F4858 #80b3d1;\n    scrollbar-width: thin;    \n}\n\n.to-do-details {\n    border: 1px solid #80b3d1;\n    min-height: 300px;\n    display: flex;\n    flex-direction: column;\n    align-items: flex-start;\n    padding: 15px;\n    gap: 0.3em;\n    overflow: auto;    \n}\n\n#right-side p {\n    text-align: center;\n    color: #2F4858;\n}\n\n#to-do-controls {\n    display: flex;\n    flex-direction: column;\n    gap: 1em; \n}\n\n#footer {\n    background-color: #2F4858;     \n    grid-column: 1 / 5;\n    grid-row: 5 / 6;    \n}\n\n#dialog-todo {\n    border: 2px outset #80b3d1;\n    border-radius: 5px;\n    box-shadow: -1px 2px 3px 1px #9aafb9;   \n    width: 200px;\n    padding: 10px;\n    position: absolute; \n    top: 60px;\n    left: calc(100% - 200px - 60px);    \n}\n\n.dialog-header {\n    display: flex;\n    justify-content: space-between;\n}\n\ndialog h2 {\n    border-bottom: 1px solid black;\n}\n\ndialog form {\n    padding-top: 10px;    \n    display: flex;\n    flex-direction: column;\n    gap: 10px;    \n}\n\ndialog p {\n    display: flex;\n    flex-direction: column;\n    gap: 2px;\n}\n\ndialog form > div {\n    display: flex;\n    flex-grow: 1;\n    justify-content: space-between;        \n}\n\ndialog button {\n    background-color: azure;\n    border-color: #80b3d1;   \n    border-radius: 5px;\n    padding: 2px 6px 2px 6px;    \n}\n\ndialog p button {\n    background-color: azure;\n    border-color: #80b3d1;   \n    border-radius: 5px;\n    width: 40%;    \n}\n\ndialog input {    \n    display: flex;\n    flex-direction: column;\n    width: 95%;\n    height: 1.4rem;\n}\n\ndialog input[type=\"date\"] {    \n    display: flex;\n    flex-direction: row;\n    width: 65%;\n    height: 1.4rem;\n}\n\n\ndialog textarea {\n    max-width: 95%;\n    min-width: 95%;\n    min-height: 1.4rem;\n    max-height: 200px;\n    padding: 5px;\n}\n"],"sourceRoot":""}]);
